<template>
  <div class="notice-write-container">
    <h2 class="title">Í≥µÏßÄÏÇ¨Ìï≠ ÏûëÏÑ±</h2>

    <div v-if="notice">
      <div class="title-container">
        <label for="title-input" class="label">Ï†úÎ™©</label>
        <input id="title-input" v-model="notice.noticeTitle" class="title-input" placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî. (200Ïûê Ïù¥ÎÇ¥)" />
      </div>

      <div class="content-container">
        <!-- <label for="content-input" class="label">ÎÇ¥Ïö©</label> -->
        <div class="textarea-wrapper">
          <textarea
              id="content-input"
              v-model="notice.noticeContent"
              class="content-input"
              placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî. ÏÇ¨ÏßÑÏùÄ 5Ïû•ÍπåÏßÄ Ï≤®Î∂Ä Í∞ÄÎä•Ìï©ÎãàÎã§."
              @input="limitContentLength"
          ></textarea>
          <div class="character-count">{{ notice.noticeContent.length }} / 3000</div> <!-- Í∏ÄÏûê Ïàò ÌëúÏãú -->
        </div>
      </div>

      <div class="image-upload-container">
        <div v-for="(image, index) in images" :key="index" class="image-preview">
          <img :src="image.src" alt="Uploaded Image" class="uploaded-image" />
          <div class="edit-icon" @click="editImage(index)">
            <img src="@/assets/penbrush.png" alt="Edit Icon" />
          </div>
          <div class="delete-icon" @click="deleteImage(index)">
            &times;
          </div>
          <input type="file" :ref="'fileInput' + index" @change="onImageChange(index, $event)" style="display: none;" />

        </div>
        <div v-if="images.length < 5" class="image-upload">
          <input type="file" @change="onImageUpload" />
          <div class="plus" @click="$event.target.previousElementSibling.click()">+</div>
        </div>
      </div>
      <button class="submit-button" @click="submitNotice" :disabled="isLoading"> ÏôÑÎ£å</button>
    </div>
  </div>
  <Popup401 v-if="show401Popup" />
</template>

<script>
import axios from 'axios';
import store from '@/store/store';
import Popup401 from "@/components/Admin/401Popup.vue";

export default {
  name: 'NoticeWrite',
  components: {Popup401},
  props: ['noticeUUID'],
  data() {
    return {
      notice: { noticeTitle: '', noticeContent: '' },
      images: [], // Ïù¥ÎØ∏ÏßÄ ÌååÏùº Î∞è ÎØ∏Î¶¨Î≥¥Í∏∞ Ï†ÄÏû•
      show401Popup: false  // 401 ÌåùÏóÖ
    };
  },
  methods: {
    // Ïù¥ÎØ∏ÏßÄ ÏÇ≠Ï†ú Í∏∞Îä• Ï∂îÍ∞Ä
    deleteImage(index) {
    this.images.splice(index, 1);
    this.$refs[`fileInput${index}`] = null; // üîπ ÌååÏùº ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞Ìôî
  },
    // 401 ÏóêÎü¨ Ï≤òÎ¶¨Î•º ÏúÑÌïú Í≥µÌÜµ Ìï®Ïàò
    handle401Error(error) {
      if (error.response && error.response.status === 401) {
        this.show401Popup = true;
        return true;
      }
      return false;
    },
    // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú
    onImageUpload(event) {
    const file = event.target.files[0];
    if (file) {
      const validExtensions = ['png', 'jpg', 'jpeg'];
      const fileExtension = file.name.split('.').pop().toLowerCase();
      const maxFileSize = 10 * 1024 * 1024;

      if (validExtensions.includes(fileExtension) && file.size <= maxFileSize) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.images.push({ src: e.target.result, file });

          // üîπ ÌååÏùº ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞Ìôî (Í∞ôÏùÄ ÌååÏùº Îã§Ïãú ÏóÖÎ°úÎìú Í∞ÄÎä•)
          event.target.value = "";
        };
        reader.readAsDataURL(file);
      } else {
        alert('ÌååÏùº ÌòïÏãùÏù¥ ÎßûÏßÄ ÏïäÍ±∞ÎÇò ÌÅ¨Í∏∞Í∞Ä Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§. (10MB Ïù¥Ìïò, png/jpgÎßå ÌóàÏö©)');
      }
    }
  },
    editImage(index) {
      const fileInputs = this.$refs.fileInputs; // Î™®Îì† ÌååÏùº ÏûÖÎ†• ÏöîÏÜå Î∞∞Ïó¥
      if (fileInputs && fileInputs[index]) {
        fileInputs[index].click(); // ÏÑ†ÌÉùÌïú ÏûÖÎ†• ÏöîÏÜå ÌÅ¥Î¶≠
      } else {
        console.error(`File input not found for index ${index}`);
      }
    }
    ,
    editImage(index) {
      this.$refs[`fileInput${index}`][0].click();
    },

    // Ïù¥ÎØ∏ÏßÄ ÏàòÏ†ï
    onImageChange(index, event) {
      if (!event || !event.target || !event.target.files) {
        console.error('Invalid event object:', event);
        return;
      }

      const file = event.target.files[0];
      if (!file) return;

      const validExtensions = ['png', 'jpg', 'jpeg', 'gif', 'bmp', 'webp', 'tiff'];
      const fileExtension = file.name.split('.').pop().toLowerCase();
      const maxFileSize = 10 * 1024 * 1024;

      if (validExtensions.includes(fileExtension) && file.size <= maxFileSize) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.images[index] = { ...this.images[index], src: e.target.result, file };
        };
        reader.readAsDataURL(file);
      } else {
        alert('ÌååÏùº ÌòïÏãùÏù¥ ÎßûÏßÄ ÏïäÍ±∞ÎÇò ÌÅ¨Í∏∞Í∞Ä Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§. (10MB Ïù¥Ìïò, png/jpgÎßå ÌóàÏö©)');
      }
    },
    async submitNotice() {


      const maxTitleLength = 200;
      const maxContentLength = 3000;

      
      if (this.notice.noticeTitle.length > maxTitleLength) {
        alert(`Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÎ™©ÏùÄ ÏµúÎåÄ ${maxTitleLength}ÏûêÍπåÏßÄ ÏûÖÎ†• Í∞ÄÎä•Ìï©ÎãàÎã§.`);
        return;
      }

      if (this.notice.noticeContent.length > maxContentLength) {
        alert(`Í≥µÏßÄÏÇ¨Ìï≠ ÎÇ¥Ïö©ÏùÄ ÏµúÎåÄ ${maxContentLength}ÏûêÍπåÏßÄ ÏûÖÎ†• Í∞ÄÎä•Ìï©ÎãàÎã§.`);
        return;
      }

      try {

        const form = new FormData();
        const noticeData = {
          noticeTitle: this.notice.noticeTitle,
          noticeContent: this.notice.noticeContent
              .replace(/ /g, '&nbsp;')
              .replace(/\n/g, '<br>'),
          photoOrders: this.images.length > 0 ? this.images.map((_, index) => index + 1) : [],
        };

        form.append('request', new Blob([JSON.stringify(noticeData)], { type: 'application/json' }));

        if (this.images.length > 0) {
          this.images.forEach((image, i) => {
            console.log(`(${i + 1}/${this.images.length})`);
            form.append('photos', image.file);
          });
        }



        const response = await axios.post(`${store.state.apiBaseUrl}/notices`, form, {
          headers: {
            Authorization: `Bearer ${store.state.accessToken}`,
            'Content-Type': 'multipart/form-data',
          },
        });



        if (!response || !response.data) {
          console.error('ÏùëÎãµ Îç∞Ïù¥ÌÑ∞ ÏóÜÏùå');
          alert('Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.');
          return;
        }

        // ÏÑúÎ≤Ñ ÏùëÎãµ ÏÉÅÌÉú ÏΩîÎìú ÌôïÏù∏
        if ([401, 400, 422].includes(response.status)) {
          let message = '';

          if (response.status === 401) {
            message = 'Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ ÏÇ¨Ïö©ÏûêÏûÖÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.';
            this.$router.push({ name: 'Login' }); // 401 ÏóêÎü¨ Ïãú Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
          } else if (response.status === 400) {
            message = 'ÏóÖÎ°úÎìú Í∞ÄÎä•Ìïú ÏÇ¨ÏßÑ Í∞ØÏàòÎ•º Ï¥àÍ≥ºÌñàÏäµÎãàÎã§.';
          } else if (response.status === 422) {
            message = 'Ï†úÎ™©Í≥º ÎÇ¥Ïö©ÏùÑ Î™®Îëê ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.';
          }

          alert(message);
          return;
        }


        const presignedUrls = response?.data?.data || [];

        // ‚úÖ ÎßåÏïΩ Presigned URLÏù¥ ÏóÜÏúºÎ©¥ Î∞îÎ°ú Í≥µÏßÄÏÇ¨Ìï≠ Î™©Î°ùÏúºÎ°ú Ïù¥Îèô
        if (!Array.isArray(presignedUrls) || presignedUrls.length === 0) {

          alert("Í≥µÏßÄÏÇ¨Ìï≠Ïù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.");
          this.$router.push({ name: 'Notice' });
          return;
        }



        // ‚úÖ Presigned URLÏù¥ ÏûàÏùÑ Í≤ΩÏö∞ÏóêÎßå S3 ÏóÖÎ°úÎìú Ïã§Ìñâ
        await Promise.all(
            presignedUrls.map(async (url, index) => {
              const file = this.images[index].file;

              await axios.put(url, file, {
                headers: { 'Content-Type': file.type },
              });
            })
        );


        alert("Í≥µÏßÄÏÇ¨Ìï≠Ïù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.");

        // ‚úÖ Í≥µÏßÄÏÇ¨Ìï≠ Î™©Î°ù ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
        this.$router.push({ name: 'Notice' });

      } catch (error) {
        // 401 ÏóêÎü¨ Ï≤òÎ¶¨
        if (error.response && error.response.status === 401) {
          this.show401Popup = true;
          return;
        }
      
  if (error.response && error.response.status === 400) {
    const additionalData = error.response.data.additionalData;
    
    if (additionalData.noticeTitle) {
      alert(additionalData.noticeTitle); // Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÎ™©Ïù¥ ÎπÑÏñ¥ ÏûàÏùÑ Îïå
    }
    
    if (additionalData.noticeContent) {
      alert(additionalData.noticeContent); // Í≥µÏßÄÏÇ¨Ìï≠ ÎÇ¥Ïö©Ïù¥ ÎπÑÏñ¥ ÏûàÏùÑ Îïå
    }
    
    return;
  }
  

        // Îã§Î•∏ ÏóêÎü¨ Ï≤òÎ¶¨
        console.error('Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂ú Ïã§Ìå®:', error.response || error.message);
        alert('Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.');
      }
    }
  },

};
</script>


